/* latin */

@font-face {
    font-family: 'Lustria';
    font-style: normal;
    font-weight: 400;
    src: local('Lustria'), local('Lustria-Regular'), url(https://fonts.gstatic.com/s/lustria/v5/9oRONYodvDEyjuhOnC8zMw.woff2) format('woff2');
    unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
  }
  
  @font-face {
    font-family: 'codetap-blog';
    src:
      url('asset/font/codetap-blog.ttf?9pqb6a') format('truetype'),
      url('asset/font/codetap-blog.woff?9pqb6a') format('woff'),
      url('asset/font/codetap-blog.svg?9pqb6a#codetap-blog') format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: block;
  }
  
  [class^="cb-"], [class*=" cb-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'codetap-blog' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
  
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  .cb-cross:before {
    content: "\e900";
  }
  .cb-menu:before {
    content: "\e901";
  }
  .cb-list:before {
    content: "\e901";
  }
  .cb-options:before {
    content: "\e901";
  }
  .cb-lines:before {
    content: "\e901";
  }
  .cb-hamburger:before {
    content: "\e901";
  }
  